<template>
  <div class="container">
		<div class="content">
			<div class="info" @click="setUserInfo">
				<img :src="portrait" />
				<p>{{ name }}</p>
			</div>
			<div class="flex">
				<div class="user_header">
					<div class="user_money">{{balance}}</div>
					<div class="user_money_text">余额（元）<van-button type="danger" size="mini"  @click="withdraw">提现</van-button></div>
				</div>
				<div class="user_header">
					<div class="user_money">{{history_money}}</div>
					<div class="user_money_text">历史总收益（元）</div>
				</div>
			</div>
		</div>

  </div>
</template>

<script>

  import {Dialog} from 'vant';

	import { USER_PROFILE } from '@/api/user';
	import bg_default from "../../assets/images/home_bg.png";
  import avatar_default from "@/assets/images/avatar_default.png";

	export default {
		name: "user-header",

		props: {
			isLogin: {
				type: Boolean,
				default: false
			}
		},

		data(){
			return {
        name: '',
        portrait: avatar_default,
        account:'',
				balance: 0,
				history_money: 0,
				background_image: bg_default
			}
		},

    created(){
			this.getUserInfo();
		},

		methods: {
			getUserInfo(){
        this.name = localStorage.name || '';
        this.account = localStorage.account || '';
				let self = this;
        self.$user.getUserInfo(function(data){
					self.balance = data.balance;
					self.history_money = data.history_money
				});
			},
      setUserInfo() {
        this.$router.push({ name: "user-information"});
      },
			toLogin(){
				!this.isLogin && this.$router.push({name: 'login', query: {redirect: "user"}})
			},
      withdraw: function() {
        if(this.balance <= 0){
          Dialog.alert({
            title: "警告",
            message: "余额不足，无法提现"
          }).then(() => {
            // on close
          });
          return;
        }
        this.$router.push({ name: "withdraw", params: {from: 'user'} });
      }
		},

    components:{
		  [Dialog.name] : Dialog
    }
	}

</script>

<style lang="scss" scoped>
	.flex {
		width: 100%;
		flex: 1;
		display: flex;
    justify-content: space-between;
		flex-wrap: nowrap;
	}
	.user_header {
		position: relative;
		/*background-repeat: no-repeat;*/
		/*background-size: cover;*/
		box-sizing: border-box;
		text-align: center;
		color: #fff;
		margin-top: 20px;
		&:nth-child(1) {
			margin-left: 10%;
		}
		&:nth-child(2) {
			margin-right: 10%;
		}
	}

	i.user_set {
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 24px;
	}
	.user_avatar{
		margin-bottom: 10px;
		img{
			border: 0;
			border-radius: 50%;
		}
	}

  .user_money{
    // margin-top: 30px;
    font-size: 22px;
    color: rgb(243, 228, 24);
  }

  .user_money_text{
		// color: #bfbfbf;
		color: #fff;
    margin-top: 10px;
  }

  .btn_withdraw{
    color: #29a0f6;
	}

	.container {
		width: 100%;
		height: 250px;
		background: url("../../assets/images/home_bg.png") no-repeat;
		background-size: 100% 100%;
	}

	.content {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.info {
			width: 60%;
			margin-top: 20px;
			text-align: center;
			img {
				width: 50px;
				height: 50px;
				padding: 10px;
				border: 2px solid rgba($color: #fff, $alpha: .4);
				border-radius: 50%;
			}
			p {
				width: 100%;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
		}
	}

</style>



