<template>
	<div class="user_module">
		<van-cell-group>
      <!-- <van-cell @click="setUserInfo" is-link >
        <template slot="title">
          <span class="van-cell-text">{{name}}</span>
          <div class="van-cell__label" >{{account}}</div>
        </template>
        <img width="55" height="55" slot="icon" :src="portrait">
      </van-cell> -->
			<van-cell icon="cash-back-record" title="收支记录" to="/user/earnings/list"  is-link />
			<van-cell icon="records" title="提现记录"  to="/user/withdraw/list" isLink />
			<van-cell icon="id-card" title="身份认证" to="/user/information/id" isLink />
			<van-cell icon="card" title="银行卡认证" to="/user/information/setBank" isLink />
			<van-cell icon="orders-o" title="系统公告" to="/message/notice-list" isLink />
      <van-cell icon="phone" title="客服电话" @click="show=true"/>
		</van-cell-group>
    <van-actionsheet v-model="show" title="联系客服">
      <p class="show_txt">有任何问题请加Q群咨询：123456</p>
    </van-actionsheet>
	</div>
</template>

<script>
  import { Dialog } from 'vant';
  import avatar_default from "@/assets/images/avatar_default.png";
	export default {
		name: "user-module",
    data(){
      return {
        name: '',
        portrait: avatar_default,
        account:'',
        activeNames: [],
        show: false
      }
    },
    activated(){
      this.getUserInfo();
    },
    methods:{
      getUserInfo(){
        let portrait = localStorage.portrait || '';
        this.portrait= portrait;
        this.name = localStorage.name || '';
        this.account = localStorage.account || '';
        if (portrait === 'null' || portrait === '' || !portrait) {
          this.portrait = avatar_default;
        }
      },
      setUserInfo() {
        this.$router.push({ name: "user-information"});
      }
    }
	}
</script>
<style scoped lang="scss">
	.user_module{
		background-color: #fff;
	}
  .van-cell-text{
    margin-left: 20px;
  }
  .show_txt {
    font-size: 14px;
    height: 80px;
    text-indent: 2rem;
  }
</style>



